/* Placeholder effetto skeleton */
.loading-placeholder {
    background: #e0e0e0;
    animation: pulse 1.5s infinite;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

@keyframes pulse {
    0% { opacity: 0.6; }
    50% { opacity: 1; }
    100% { opacity: 0.6; }
}

.map-container {
    position: relative;
    width: 100%;
    height: 100%;
}
